'use client';
import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import SplitText from 'gsap/dist/SplitText';
import SmokeBg from '../SmokeBg/SmokeBg';
import { useGSAP } from '@gsap/react';
import { useTypeformModal } from '@/lib/ModalContextProvider';
import Button from '../Button/Button';

const HeroSection = () => {
  const sectionRef = useRef(null);

  useGSAP(() => {
    const mm = gsap.matchMedia();
    mm.add(
      {
        small: '(max-width: 767px)',
        large: '(min-width: 768px)',
      },
      (ctx) => {
        const { small } = ctx.conditions;

        let heroHeading = new SplitText('.hero-heading', {
          type: 'chars, words',
          wordsClass: 'word',
          charsClass: 'reveal-child',
          // linesClass: 'overflow-hidden',
        });
        let heroHeadingText = heroHeading.chars;

        gsap.set(heroHeadingText, {
          autoAlpha: 1,
          scale: 1,
          filter: 'blur(0px)',
          y: 0,
          // filter: 'blur(10px)',
        });
        const elements = sectionRef.current.querySelectorAll('.blur-fade');

        gsap.set(elements, {
          opacity: 0,
          filter: 'blur(10px)',
          y: 10,
        });

        const tl = gsap.timeline({});

        tl.to(
          '.hero-heading',

          {
            duration: 0.5,
            autoAlpha: 1,
          }
        );
        tl.from(
          heroHeadingText,

          {
            duration: 0.5,
            stagger: 0.09,
            scale: 1.2,
            filter: 'blur(10px)',
            opacity: 0,
            y: 10,
            // filter: 'blur(0px)',
            ease: 'cubic-bezier(0, 0.2, 0.2, 1)',
          }
        );

        tl.to(
          elements,
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            stagger: 0.2,
            ease: 'power2.out',
          },
          '< 0.5'
        );
      }
    );
  }, {});
  const { setVisibleModal } = useTypeformModal();
  return (
    <section ref={sectionRef} className="relative isolate">
      <SmokeBg />
      <div className="container">
        <div className="border-x border-dashed border-x-white/20 pt-[140px] pb-10">
          <div className="text-center max-w-[870px] mx-auto">
            <h1 className="text-4xl md:text-7xl font-bold mb-6 text-white hero-heading opacity-0 tracking-[-1px] lg:tracking-[-3px]">
              <span className="gradient-text">Autonomous DevOps</span> for
              modern enterprise
            </h1>

            <p className="text-white/50 text-sm md:text-xl mb-6 blur-fade opacity-0">
              Centralized platform for CICD, IaC, Config Management and
              Operations with{' '}
              <span className="text-white">AI driven agents.</span>
            </p>
            <div className=" blur-fade opacity-0">
              <Button onClick={() => setVisibleModal('demo')}>
                Demo with founder
              </Button>
            </div>
          </div>

          <div className="pt-10 md:pt-0 max-w-[550px] mx-auto">
            <video
              src="/img/hero-video.mp4"
              className="w-full mix-blend-screen"
              autoPlay
              loop
              muted
              playsInline
            ></video>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
